'use strict';

// MODULES //

import sindf from '@stdlib/math-base-special-sindf';
import cosdf from '@stdlib/math-base-special-cosdf';


// MAIN //

/**
* Simultaneously computes the sine and cosine of a single-precision floating-point number (in degrees) and assigns the results to a provided output array.
*
* @private
* @param {number} x - input value (in degrees)
* @param {Collection} out - output array
* @param {integer} stride - output array stride
* @param {NonNegativeInteger} offset - output array index offset
* @returns {Collection} output array
*
* @example
* var out = [ 0.0, 0.0 ];
* var v = sincosdf( 0.0, out, 1, 0 );
* // returns [ ~0.0, ~1.0 ]
*
* @example
* var out = [ 0.0, 0.0 ];
* var v = sincosdf( 90.0, out, 1, 0 );
* // returns [ ~1.0, ~0.0 ]
*
* @example
* var out = [ 0.0, 0.0 ];
* var v = sincosdf( -90.0, out, 1, 0 );
* // returns [ ~-1.0, ~0.0 ]
*
* @example
* var out = [ 0.0, 0.0 ];
* var v = sincosdf( NaN, out, 1, 0 );
* // returns [ NaN, NaN ]
*
*/
function sincosdf( x, out, stride, offset ) {
	out[ offset ] = sindf( x );
	out[ offset + stride ] = cosdf( x );
	return out;
}


// EXPORTS //

export default sincosdf;
