'use strict';

// MODULES //

import assign from './assign.js';


// MAIN //

/**
* Simultaneously computes the sine and cosine of a single-precision floating-point number (in degrees).
*
* @param {number} x - input value (in degrees)
* @returns {Array<number>} sine and cosine
*
* @example
* var v = sincosdf( 0.0 );
* // returns [ ~0.0, ~1.0 ]
*
* @example
* var v = sincosdf( 90.0 );
* // returns [ ~1.0, ~0.0 ]
*
* @example
* var v = sincosdf( -90.0 );
* // returns [ ~-1.0, ~0.0 ]
*
* @example
* var v = sincosdf( NaN );
* // returns [ NaN, NaN ]
*/
function sincosdf( x ) {
	return assign( x, [ 0.0, 0.0 ], 1, 0 );
}


// EXPORTS //

export default sincosdf;
