'use strict';

/**
* Simultaneously compute the sine and cosine of a single-precision floating-point number (in degrees).
*
* @module @stdlib/math-base-special-sincosdf
*
* @example
* import sincosdf from '@stdlib/math-base-special-sincosdf';
*
* var v = sincosdf( 0.0 );
* // returns [ ~0.0, ~1.0 ]
*
* v = sincosdf( 90.0 );
* // returns [ ~1.0, ~0.0 ]
*
* v = sincosdf( -30.0 );
* // returns [ ~-0.5, ~0.866 ]
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import sincosdf from '@stdlib/math-base-special-sincosdf';
*
* var out = new Float32Array( 2 );
*
* var v = sincosdf.assign( 0.0, out, 1, 0 );
* // returns <Float32Array>[ ~0.0, ~1.0 ]
*
* var bool = ( v === out );
* // returns true
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;
